import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";
import { useFrame } from "@react-three/fiber";

export const Earth = (props) => {
  const group = useRef();
  const { nodes, materials } = useGLTF("/scene.gltf");

  useFrame((state, delta) => {
    group.current.rotation.y += 0.2 * delta;
    const t = state.clock.getElapsedTime();
    group.current.rotation.set(
      Math.cos(t / 4) / 8,
      Math.sin(t / 3) / 4,
      0.15 + Math.sin(t / 2) / 8
    );
    group.current.position.y = (0.5 + Math.cos(t / 2)) / 7;
  });
  return (
    <group ref={group} {...props} dispose={null}>
      <mesh
        fov={90}
        scale={8}
        position={[0, 0, 0]}
        castShadow
        receiveShadow
        geometry={nodes.Heart_1ct001.geometry}
        material={materials["Material"]}
        rotation={[0, 0, 0]}
      />
      <pointLight position={[0, 0, 0]} intensity={1} color="white" />
      <hemisphereLight position={[0, 0, 0]} intensity={1} color="white" />
    </group>
  );
};

useGLTF.preload("/scene.gltf");
