import React, { useEffect } from "react";
import "./App.css";
import img1 from "./img/img1.jpg";

const RunAwayButton = () => {
  useEffect(() => {
    const wrapper = document.querySelector(".wrapper");
    const question = document.querySelector(".question");
    const yesBtn = document.querySelector(".yes-btn");
    const noBtn = document.querySelector(".no-btn");

    const wrapperRect = wrapper.getBoundingClientRect();
    const noBtnRect = noBtn.getBoundingClientRect();

    const handleClick = () => {
      question.innerHTML = "Love you too :)";
    };

    const handleMouseOver = () => {
      const i =
        Math.floor(Math.random() * (wrapperRect.width - noBtnRect.width)) + 1;
      const j =
        Math.floor(Math.random() * (wrapperRect.height - noBtnRect.height)) + 1;

      noBtn.style.left = i + "px";
      noBtn.style.top = j + "px";
    };

    yesBtn.addEventListener("click", handleClick);
    noBtn.addEventListener("mouseover", handleMouseOver);

    // Clean up event listeners when the component unmounts
    return () => {
      yesBtn.removeEventListener("click", handleClick);
      noBtn.removeEventListener("mouseover", handleMouseOver);
    };
  }, []); // Empty dependency array ensures that this effect runs once after the initial render

  return (
    <div className="main">
      <div className="wrapper">
      <img src={img1} alt="My Image" className="img1"/>
      <div className="question">Do you love me?</div>
      <div className="btn-group">
        <button className="yes-btn">Yes</button>
        <button className="no-btn">No</button>
      </div>
      </div>
    </div>
  );
};

export default RunAwayButton;
